@import "../mixins";
@import "../colors";

body {
	font-size: 18px;
	font-weight: 200;
	line-height: 32px;
	color: #4e4b4b;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

.h1 {
	font-size: 90px;
	line-height: 96px;
	font-weight: 200;

	.em {
		font-weight: 600;
	}
}

.h2 {
	font-size: 74px;
	line-height: 80px;
	font-weight: 100;
	color: #272727;
	margin-bottom: 90px;

	.em {
		color: #a5a1a1;
	}
}

.h3 {
	font-size: 52px;
	line-height: 60px;
	font-weight: 100;
}

.h4 {
	font-size: 36px;
	line-height: 46px;
	font-weight: 600;
}

.h5 {
	margin-top: 80px;
	margin-bottom: 40px;
	font-size: 13px;
	font-weight: 400;
	letter-spacing: 1px;
	text-transform: uppercase;
	color: #272727;
}

@include media-breakpoint-down(md) {

	.h1 {
		font-size: 60px;
		line-height: 60px;
	}

	.h2 {
		font-size: 50px;
		line-height: 50px;
	}

	.h3 {
		font-size: 35px;
		line-height: 35px;
	}

	.h4 {
		font-size: 25px;
		line-height: 25px;
	}

	.h5 {
		font-size: 13px;
		line-height: 20px;
	}

}

.background--dark {
	color: white;

	.h1,
	.h2,
	.h3,
	.h4,
	.h5 {
		color: white;
	}
}
