.section--events-listing {
	padding: 180px 0 50px;

	.h2 {
		max-width: 760px;
		margin-bottom: 0;
	}

}

.events-listing {
	width: 100%;
}

.event__item {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	color: inherit;
	padding: 20px 15px;
	margin-bottom: 20px;
	border-top: 1px solid #eee;

	&:last-child {
		border-bottom: 1px solid #eee;
	}

	.date {
		display: block;
		font-size: 14px;
		font-weight: 900;
		line-height: 23px;
		margin-right: 30px;
	}

	.title {
		display: block;
		font-size: 22px; // 30px;
		line-height: 25px; // 36px;
		font-weight: 200;
		color: #272727;
		margin-right: 30px;
		flex: 1 1 auto;

		&>span {
			display: inline-block;
			max-width: 700px;
		}
	}

	.location {
		display: block;
		font-size: 18px;
		line-height: 20px;
		font-weight: 200;
		color: #272727;
		margin-right: 30px;
		max-width: 200px;
		flex: 0 0 200px;
	}

	.btn--discover {
		white-space: nowrap;
	}

	&:hover,
	&:active,
	&:visited {
		color: inherit;
		box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.25);
	}

	@include media-breakpoint-down(sm) {
		align-items: flex-start;
		flex-wrap: wrap;

		.title {
			order: 0;
			padding-bottom: 40px;
			width: 100%;
			flex: 1 0 100%;
		}

		.date {
			order: 1;

		}

		.location {
			order: 2;
		}

		.btn--discover {
			order: 3;
			margin-right: 50%;
			margin-top: 10px;
		}
	}
}
